/*
custom.less is where custom styles will be applied to specific elements
 */

.button {
  padding: 20px;
  transform: scale(.5);
  appearance: none;
  -webkit-border-radius: 12px;
  border-radius: 12px;
}

.l-page-layout__main {
  border: 1px solid @color-red;
}

.c-page-header__title {
  text-decoration: underline;
  color: @color-green;
}

body {
  display: flex;
}
